import React,{Component} from 'react';
import FluxCRUDActions from '../../actions/FluxCRUDActions';
import styles from './FluxCRUDFooter.css';
import withStyles from '../../decorators/withStyles';


var ReactPropTypes = React.PropTypes;

@withStyles(styles)
class FluxCRUDFooter extends Component {

  /**
   * 约束prop数据类型
   */
  static propTypes = {
    allFluxCRUDs: ReactPropTypes.object.isRequired
  };

  /**
   * 构造函数
   */
  constructor() {
    super();
  }

  /**
   * 渲染虚拟DOM
   * @return {object}
   */
  render() {
    var allFluxCRUDs = this.props.allFluxCRUDs;
    var total = Object.keys(allFluxCRUDs).length;

    if (total === 0) {
      return null;
    }

    var completed = 0;
    for (var key in allFluxCRUDs) {
      if (allFluxCRUDs[key].complete) {
        completed++;
      }
    }

    var itemsLeft = total - completed;
    var itemsLeftPhrase = '个';
    itemsLeftPhrase += '剩余';

    // 如果有选中则显示删除按钮
    var clearCompletedButton;
    if (completed) {
      clearCompletedButton =
        <button
          className="clear-completed"
          onClick={this._onClearCompletedClick.bind(this)}>
          清除选中 ({completed})
        </button>;
    }

  	return (
      <footer className="footer">
        <span className="count">
          <strong>
            {itemsLeft}
          </strong>
          {itemsLeftPhrase}
        </span>
        {clearCompletedButton}
      </footer>
    );
  }

  /**
   * 删除所有选中todos的事件处理函数
   */
  _onClearCompletedClick() {
    FluxCRUDActions.destroyCompleted();
  }

}

export default FluxCRUDFooter;



