import React, { PropTypes } from 'react';
import styles from './FluxCRUDPage.css';
import withStyles from '../../decorators/withStyles';
import FluxCRUDApp from './../../components/FluxCRUD/FluxCRUD'


@withStyles(styles)
class FluxCRUDPage {

  static contextTypes = {
    onSetTitle: PropTypes.func.isRequired
  };

  render() {
    let title = 'FluxCRUD';
    this.context.onSetTitle(title);
    return (
      <div className="FluxCRUDPage">
        <div className="FluxCRUDPage-container">
          <h1>{title}</h1>
          <p>Flux通信实现的增删改查操作</p>
          <FluxCRUDApp></FluxCRUDApp>
        </div>
      </div>
    );
  }
}

export default FluxCRUDPage;



