import AppDispatcher from '../core/Dispatcher.js';
import FluxCRUDConstants from '../constants/FluxCRUDConstants';

var FluxCRUDActions = {

  /**
   * @param  {string} text
   */
  create: function(text) {
    AppDispatcher.dispatch({
      actionType: FluxCRUDConstants.FLUX_CRUD_CREATE,
      text: text
    });
  },

  /**
   * @param  {string} id
   * @param  {string} text
   */
  updateText: function(id, text) {
    AppDispatcher.dispatch({
      actionType: FluxCRUDConstants.FLUX_CRUD_UPDATE_TEXT,
      id: id,
      text: text
    });
  },

  /**
   * 切换完成状态
   * @param  {object} fluxCRUD
   */
  toggleComplete: function(fluxCRUD) {
    var id = fluxCRUD.id;
    var actionType = fluxCRUD.complete ?
        FluxCRUDConstants.FLUX_CRUD_UNDO_COMPLETE :
        FluxCRUDConstants.FLUX_CRUD_COMPLETE;

    AppDispatcher.dispatch({
      actionType: actionType,
      id: id
    });
  },

  /**
   * 标记所有为完成
   */
  toggleCompleteAll: function() {
    AppDispatcher.dispatch({
      actionType: FluxCRUDConstants.FLUX_CRUD_TOGGLE_COMPLETE_ALL
    });
  },

  /**
   * @param  {string} id
   */
  destroy: function(id) {
    AppDispatcher.dispatch({
      actionType: FluxCRUDConstants.FLUX_CRUD_DESTROY,
      id: id
    });
  },

  /**
   * 删除所有选中
   */
  destroyCompleted: function() {
    AppDispatcher.dispatch({
      actionType: FluxCRUDConstants.FLUX_CRUD_DESTROY_COMPLETED
    });
  }

};

export default FluxCRUDActions;



