import React,{Component} from 'react';
import FluxCRUDActions from '../../actions/FluxCRUDActions';
import FluxCRUDTextInput from './../FluxCRUDTextInput';
import styles from './FluxCRUDHeader.css';
import withStyles from '../../decorators/withStyles';

@withStyles(styles)
class FluxCRUDHeader extends Component {

  /**
   * @return {object}
   */
  render() {
    return (
      <header className="header">
        <FluxCRUDTextInput
          className="new"
          placeholder="输入内容回车"
          onSave={this._onSave}
        />
      </header>
    );
  }

  /**
   * 提供给FluxCRUDTextInput调用的保存事件处理函数
   * 这样定义使得FluxCRUDTextInput以不同方式多处应用
   * @param {string} text
   */
  _onSave= text => {
    if (text.trim()){
      FluxCRUDActions.create(text);
    }

  }

}

export default FluxCRUDHeader;



