import React,{Component} from 'react';
import FluxCRUDActions from '../../actions/FluxCRUDActions';
import FluxCRUDItem from './../FluxCRUDItem';
import styles from './FluxCRUDMainSection.css';
import withStyles from '../../decorators/withStyles';

var ReactPropTypes = React.PropTypes;

@withStyles(styles)
class FluxCRUDMainSection extends Component {

  static propTypes = {
    allFluxCRUDs: ReactPropTypes.object.isRequired,
    areAllComplete: ReactPropTypes.bool.isRequired
  };

  /**
   * @return {object}
   */
  render() {
    // todos中没有值的时候渲染空的DOM，即隐藏这个section
    if (Object.keys(this.props.allFluxCRUDs).length < 1) {
      return null;
    }

    var allFluxCRUDs = this.props.allFluxCRUDs;
    var todos = [];

    for (var key in allFluxCRUDs) {
      todos.push(<FluxCRUDItem key={key} todo={allFluxCRUDs[key]}/>);
    }

    return (
      <section className="main">
        <input
          className="toggle-all"
          id="toggle-all"
          type="checkbox"
          onChange={this._onToggleCompleteAll}
          checked={this.props.areAllComplete ? 'checked' : ''}
          />
        <label htmlFor="toggle-all">全选</label>
        <ul className="list">{todos}</ul>
      </section>
    );
  }

  /**
   * 所有todos都完成的事件的处理函数
   */
  _onToggleCompleteAll() {
    FluxCRUDActions.toggleCompleteAll();
  }

}

export default FluxCRUDMainSection;



