import React from 'react';
import styles from './Header.css';
import withStyles from '../../decorators/withStyles';
import RouteLink from '../RouteLink';
import Navigation from '../Navigation';

@withStyles(styles)
class Header {
  render() {
    return (
      <div className="Header">
        <div className="Header-container">
          <a className="Header-brand" href="/" onClick={RouteLink.handleClick}>
            <img className="Header-brandImg" src={require('./logo-small.png')} width="38" height="38" alt="React" />
            <span className="Header-brandTxt">京东移动前端</span>
          </a>
          <Navigation className="Navigation" />
          <div className="Header-banner">
            <h1 className="Header-bannerTitle">JDM</h1>
            <p className="Header-bannerDesc">前端组件化 前端工程化 JS,CSS,HTML依赖管理 ES6 打包 压缩混淆 React 虚拟DOM 前端路由 单页面应用</p>
          </div>
        </div>
      </div>
    );
  }

}
export default Header;



