import React from 'react';
import styles from './Feedback.css';
import withStyles from '../../decorators/withStyles';

@withStyles(styles)
class Feedback {

  render() {
    return (
      <div className="Feedback">
        <div className="Feedback-container">
          <a className="Feedback-link" href="https://gitter.im/zrwdopod/jdm" target="_blank">咨询问题</a>
          <span className="Feedback-spacer">|</span>
          <a className="Feedback-link" href="https://github.com/zrwdopod/jdm/issues/new" target="_blank">提交BUG</a>
        </div>
      </div>
    );
  }

}

export default Feedback;



