import AppDispatcher from '../core/Dispatcher.js';
import {EventEmitter} from 'events';
import FluxCRUDConstants from '../constants/FluxCRUDConstants';
import assign from 'object-assign';

var CHANGE_EVENT = 'change';

var _fluxCRUDs = {};

/**
 * 新增一个项
 * @param  {string} 新增项的文本
 */
function create(text) {
  // AJAX去持久数据暂时没有提供
  // 服务端存储
  // 当前时间戳和一个随机数组成的id
  var id = (+new Date() + Math.floor(Math.random() * 999999)).toString(36);
  _fluxCRUDs[id] = {
    id: id,
    complete: false,
    text: text
  };
}

/**
 * 更新
 * @param  {string} id
 * @param {object} 只更新包含数据的对象
 *     updated.
 */
function update(id, updates) {
  _fluxCRUDs[id] = assign({}, _fluxCRUDs[id], updates);
}

/**
 * 用同一个对象更新所有项
 * @param  {object} 只更新包含数据的对象
 *     updated.
 */
function updateAll(updates) {
  for (var id in _fluxCRUDs) {
    update(id, updates);
  }
}

/**
 * 删除一项
 * @param  {string} id
 */
function destroy(id) {
  delete _fluxCRUDs[id];
}

/**
 * 删除所有选中项
 */
function destroyCompleted() {
  for (var id in _fluxCRUDs) {
    if (_fluxCRUDs[id].complete) {
      destroy(id);
    }
  }
}

var FluxCRUDStore = assign({}, EventEmitter.prototype, {

  /**
   * 检测所有剩余项是否标记为完成状态
   * @return {boolean}
   */
  areAllComplete: function() {
    for (var id in _fluxCRUDs) {
      if (!_fluxCRUDs[id].complete) {
        return false;
      }
    }
    return true;
  },

  /**
   * 获取全部集合
   * @return {object}
   */
  getAll: function() {
    return _fluxCRUDs;
  },

  emitChange: function() {
    this.emit(CHANGE_EVENT);
  },

  /**
   * @param {function} callback
   */
  addChangeListener: function(callback) {
    this.on(CHANGE_EVENT, callback);
  },

  /**
   * @param {function} callback
   */
  removeChangeListener: function(callback) {
    this.removeListener(CHANGE_EVENT, callback);
  }
});

// 注册所有动作的处理函数
AppDispatcher.register(function(action) {
  var text;

  switch(action.actionType) {
    case FluxCRUDConstants.FLUX_CRUD_CREATE:
      text = action.text.trim();
      if (text !== '') {
        create(text);
        FluxCRUDStore.emitChange();
      }
      break;

    case FluxCRUDConstants.FLUX_CRUD_TOGGLE_COMPLETE_ALL:
      if (FluxCRUDStore.areAllComplete()) {
        updateAll({complete: false});
      } else {
        updateAll({complete: true});
      }
      FluxCRUDStore.emitChange();
      break;

    case FluxCRUDConstants.FLUX_CRUD_UNDO_COMPLETE:
      update(action.id, {complete: false});
      FluxCRUDStore.emitChange();
      break;

    case FluxCRUDConstants.FLUX_CRUD_COMPLETE:
      update(action.id, {complete: true});
      FluxCRUDStore.emitChange();
      break;

    case FluxCRUDConstants.FLUX_CRUD_UPDATE_TEXT:
      text = action.text.trim();
      if (text !== '') {
        update(action.id, {text: text});
        FluxCRUDStore.emitChange();
      }
      break;

    case FluxCRUDConstants.FLUX_CRUD_DESTROY:
      destroy(action.id);
      FluxCRUDStore.emitChange();
      break;

    case FluxCRUDConstants.FLUX_CRUD_DESTROY_COMPLETED:
      destroyCompleted();
      FluxCRUDStore.emitChange();
      break;

    default:
      // no op
  }
});

export default FluxCRUDStore;



