import React, { PropTypes } from 'react';
import classNames from 'classnames';
import styles from './Navigation.css';
import withStyles from '../../decorators/withStyles';
import RouteLink from '../RouteLink';

@withStyles(styles)
class Navigation {

  static propTypes = {
    className: PropTypes.string
  };

  render() {
    return (
      <div className={classNames(this.props.className, 'Navigation')} role="navigation">
        <a className="Navigation-link" href="/fluxCRUD" onClick={RouteLink.handleClick}>FluxCRUD</a>
        <a className="Navigation-link" href="/about" onClick={RouteLink.handleClick}>关于</a>
        <a className="Navigation-link" href="/contact" onClick={RouteLink.handleClick}>联系我们</a>
        <span className="Navigation-spacer"> | </span>
        <a className="Navigation-link" href="/login" onClick={RouteLink.handleClick}>登录</a>
        <span className="Navigation-spacer">or</span>
        <a className="Navigation-link" href="/register" onClick={RouteLink.handleClick}>注册</a>
      </div>
    );
  }

}

export default Navigation;



