import React, { PropTypes } from 'react';
import styles from './Footer.css';
import withViewport from '../../decorators/withViewport';
import withStyles from '../../decorators/withStyles';
import RouteLink from '../RouteLink';

@withViewport
@withStyles(styles)
class Footer {

  static propTypes = {
    viewport: PropTypes.shape({
      width: PropTypes.number.isRequired,
      height: PropTypes.number.isRequired
    }).isRequired
  };

  render() {
    // CSS渲染示例
    let { width, height } = this.props.viewport;
    this.renderCss(`.Footer-viewport:after {content:' ${width}x${height}';}`);

    return (
      <div className="Footer">
        <div className="Footer-container">
          <span className="Footer-text">© 京东成都研究院</span>
          <span className="Footer-spacer">·</span>
          <a className="Footer-link" href="/" onClick={RouteLink.handleClick}>首页</a>
          <span className="Footer-spacer">·</span>
          <a className="Footer-link" href="/privacy" onClick={RouteLink.handleClick}>隐私</a>
          <span className="Footer-spacer">·</span>
          <a className="Footer-link" href="/not-found" onClick={RouteLink.handleClick}>404页</a>
          <span className="Footer-spacer"> | </span>
          <span ref="viewport" className="Footer-viewport Footer-text Footer-text--muted">窗口分辨率:</span>
        </div>
      </div>
    );
  }

}

export default Footer;



