/**
 * 这个组件被定义成一个控制器视图，监听FluxCRUDStore变化，并透传数据给其子组件
 *
 */
import React, { Component,PropTypes } from 'react';
import FluxCRUDActions from '../../actions/FluxCRUDActions';
import FluxCRUDStore from './../../stores/FluxCRUDStore';
import Header from './../FluxCRUDHeader';
import MainSection from './../FluxCRUDMainSection';
import Footer from './../FluxCRUDFooter';
import styles from './FluxCRUD.css';
import withStyles from '../../decorators/withStyles';

/**
 * 从FluxCRUDStore中获取最新数据
 */
function getFluxCRUDState() {
  return {
    allFluxCRUDs: FluxCRUDStore.getAll(),
    areAllComplete: FluxCRUDStore.areAllComplete()
  };
}
@withStyles(styles)
class FluxCRUD extends Component {

  constructor() {
    super();
    this.state = getFluxCRUDState();
  }

  /**
   * FluxCRUDStore的change事件处理函数
   */
  _onChange() {
    this.setState(getFluxCRUDState());
  }

  componentDidMount() {
    // 注册FluxCRUDStore变化后的处理函数
    FluxCRUDStore.addChangeListener(this._onChange.bind(this));
  }

  componentWillUnmount() {
    // 移除FluxCRUDStore变化后的处理函数
    FluxCRUDStore.removeChangeListener(this._onChange.bind(this));
  }

  /**
   * @return {object}
   */
  render() {
    return (
      <div className="flux-crud">
        <Header />
        <MainSection
          allFluxCRUDs={this.state.allFluxCRUDs}
          areAllComplete={this.state.areAllComplete}
          />
        <Footer allFluxCRUDs={this.state.allFluxCRUDs}/>
      </div>
    );
  }

}

export default FluxCRUD;



