import React,{Component} from 'react';
import FluxCRUDActions from '../../actions/FluxCRUDActions';
import FluxCRUDTextInput from './../FluxCRUDTextInput';
import classNames from 'classnames';
import styles from './FluxCRUDItem.css';
import withStyles from '../../decorators/withStyles';

let ReactPropTypes = React.PropTypes;

@withStyles(styles)
class FluxCRUDItem extends Component {

  static propTypes = {
    todo: ReactPropTypes.object.isRequired
  };

  constructor() {
    super();
    this.state = {
      isEditing: false
    };
  }

  /**
   * @return {object}
   */
  render() {
    let todo = this.props.todo;

    let input;
    if (this.state.isEditing) {
      input =
        <FluxCRUDTextInput
          className="edit"
          onSave={this._onSave}
          value={todo.text}
          />;
    }
    // 编辑状态时列表项设置‘editing’到class属性,当被标记为完成时设置'completed'
    // 注意'completed'时一个class,而'complete'是状态值
    // 这样区分开命名在view执行toggleComplete和destroyCompleted时候非常重要
    return (
      <li
        className={classNames({
          'completed': todo.complete,
          'editing': this.state.isEditing
        })}
        key={todo.id}>
        <div className="view">
          <input
            className="toggle"
            type="checkbox"
            checked={todo.complete}
            onChange={this._onToggleComplete.bind(this)}
            />
          <label onDoubleClick={this._onDoubleClick.bind(this)}>
            {todo.text}
          </label>
          <button className="destroy" onClick={this._onDestroyClick.bind(this)}/>
        </div>
        {input}
      </li>
    );
  }

  _onToggleComplete() {
    FluxCRUDActions.toggleComplete(this.props.todo);
  }

  _onDoubleClick() {
    this.setState({isEditing: true});
  }

  /**
   * 提供给FluxCRUDTextInput调用的保存事件处理函数
   * 这样定义使得FluxCRUDTextInput以不同方式多处应用
   * @param  {string} text
   */
  _onSave = text => {
    FluxCRUDActions.updateText(this.props.todo.id, text);
    this.setState({isEditing: false});
  }

  _onDestroyClick() {
    FluxCRUDActions.destroy(this.props.todo.id);
  }

}

export default FluxCRUDItem;



